import { graphql } from "gatsby";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import { toggleNavigationGroup } from "../../state/common";
import { IconContainer } from "./icon-container";
import { Link } from "./link";
import ArrowDownIconSvg from "../../images/arrow-down.svg";
import ArrowUpIconSvg from "../../images/arrow-up.svg";
import ProductSwitcherIconSvg from "../../images/th-large.svg";
export const DocPageNavigation = ({ data, }) => {
    const expandedPaths = useSelector((state) => state.common.expandedPaths);
    const dispatch = useDispatch();
    const handleToggleExpand = (path) => {
        dispatch(toggleNavigationGroup({ path }));
    };
    const buildNavigationStructure = (items, basePath) => (<NavigationList>
      {items.map(({ path, title, items: subItems }) => {
        const itemPath = !subItems && path === "index" ? basePath : basePath + "/" + path;
        return (<NavigationItem key={itemPath}>
            {subItems ? (<NavigationGroup expanded={expandedPaths.indexOf(itemPath) !== -1}>
                <NavigationGroupToggle onClick={() => handleToggleExpand(itemPath)}>
                  {title}
                  <IconContainer size={16}>
                    <ArrowDownIconSvg className="arrow-down"/>
                    <ArrowUpIconSvg className="arrow-up"/>
                  </IconContainer>
                </NavigationGroupToggle>
                <NavigationGroupContent>
                  {buildNavigationStructure(subItems, itemPath)}
                </NavigationGroupContent>
              </NavigationGroup>) : (<NavigationLink to={itemPath}>{title}</NavigationLink>)}
          </NavigationItem>);
    })}
    </NavigationList>);
    return (<Navigation>
      <FixedContainer>
        <ProductSwitcher>
          {data.config?.products &&
        data.config.products.find((product) => product?.path === "hotchocolate")?.title}
          <IconContainer size={16}>
            <ProductSwitcherIconSvg />
          </IconContainer>
        </ProductSwitcher>
        {data.config?.products &&
        data.config.products[0]?.items &&
        buildNavigationStructure(data.config.products[0].items
            .filter((item) => !!item)
            .map((item) => ({
            path: item.path,
            title: item.title,
            items: item.items
                ? item?.items
                    .filter((item) => !!item)
                    .map((item) => ({
                    path: item.path,
                    title: item.title,
                }))
                : undefined,
        })), "/docs/" + data.config.products[0].path)}
      </FixedContainer>
    </Navigation>);
};
export const DocPageNavigationGraphQLFragment = graphql `
  fragment DocPageNavigation on Query {
    config: file(
      sourceInstanceName: { eq: "docs" }
      relativePath: { eq: "docs.json" }
    ) {
      products: childrenDocsJson {
        path
        title
        description
        items {
          path
          title
          items {
            path
            title
          }
        }
      }
    }
  }
`;
const Navigation = styled.nav `
  display: none;
  flex: 0 0 250px;
  flex-direction: column;

  * {
    user-select: none;
  }

  @media only screen and (min-width: 1050px) {
    display: flex;
  }
`;
const FixedContainer = styled.div `
  position: fixed;
  padding: 25px 0 250px;
  width: 250px;
`;
const ProductSwitcher = styled.div `
  display: flex;
  flex-direction: row;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 6px 14px 20px;
  padding: 7px 5px;
  font-size: 0.833em;
  transition: background-color 0.2s ease-in-out;

  > ${IconContainer} {
    margin-left: auto;

    > svg {
      fill: #666;
    }
  }

  :hover {
    background-color: #ddd;
  }
`;
const NavigationList = styled.ol `
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0 20px 20px;
`;
const NavigationItem = styled.li `
  flex: 0 0 auto;
  margin: 5px 0;
  padding: 0;
  min-height: 20px;
  list-style-type: none;
`;
const NavigationGroupToggle = styled.div `
  display: flex;
  flex-direction: row;
  align-items: center;
  min-height: 20px;
  font-size: 0.833em;
`;
const NavigationGroupContent = styled.div `
  > ${NavigationList} {
    padding: 5px 0;
  }
`;
const NavigationGroup = styled.div `
  display: flex;
  flex-direction: column;
  cursor: pointer;

  > ${NavigationGroupContent} {
    display: ${(props) => (props.expanded ? "initial" : "none")};
  }

  > ${NavigationGroupToggle} > ${IconContainer} {
    margin-left: auto;

    > .arrow-down {
      display: ${(props) => (props.expanded ? "none" : "initial")};
      fill: #666;
    }

    > .arrow-up {
      display: ${(props) => (props.expanded ? "initial" : "none")};
      fill: #666;
    }
  }
`;
const NavigationLink = styled(Link) `
  font-size: 0.833em;
  color: #666;

  :hover {
    color: #000;
  }
`;
